import requestHttps from '../../common/request.js'
export default {
	data() {
		return {
			serverImg:getApp().globalData.serverImg,
			twoTab:['精选','销量','最新','价格'],
			scut:0,
			prolodding:2,
			secdList:[ ],
			medix:true,
			gohidden:true,
			flag:true,
			page:1,
			liloading:true,
			tsort:'new',
			id:'',
			kw:'',
			type:0,
		}
	},
	methods: {
		// 销量
		twonav:function(e){
			console.log(e)
			var that=this
			var cur=e.currentTarget.dataset.sect
			
			if(cur==0){
				that.tsort='new'
			}else if(cur==1){
				that.tsort='sale_num_desc'
			}else if(cur==2){
				that.tsort='date_time'
			}else if(cur==3){
				that.tsort='price_asc'
			}else{
				
			}
			that.liloading=false
			that.flag=true
			that.secdList=[]
			that.scut=cur
			that.page=1
			that.goodList()
		},
		 // 返回顶部
		 gotop:function(e){
		 	uni.pageScrollTo({
		 		scrollTop:0,
		 		duration:300
		 	})
		 },
		 // 详情
		 gopart:function(e){
			 var id=e.currentTarget.dataset.goods_id
			 uni.navigateTo({
			 	url: '../productdetail/productdetail?id='+id,
			 	success: res => {},
			 	fail: () => {},
			 	complete: () => {}
			 });
		 },
		 // 搜索
		 gosear:function(){
			 uni.switchTab({
			 	url: '../search/search',  
			 });
		 },
		 // 返回上个页面
		 goback:function(){
		 	uni.navigateBack({
		 		delta:1
		 	})
		 },
		 goodList:function(){
			 var that=this
			 var url='/tb/goods/lists'
			if(that.flag){
				console.log(that.type)
				console.log('地方大幅度分')
				if(that.type ==0 ||that.type ==undefined || that.type == 'undefined'){
					var url='/tb/goods/lists'
					var data={
						type:that.type,
						page:that.page,
						sort:that.tsort,
						id:that.id,
						kw:that.kw
					}
				}else{
					if(that.type=='hot'){
						var url='/tb/bk/lists'
						var data={
							type:that.type,
							page:that.page,
							sort:that.tsort
						}
					}else{
						var url='/tb/goods/lists'
						var data={
							type:that.type,
							page:that.page,
							sort:that.tsort
						}
					}
					
				}
				
				
				requestHttps({ 
					url:url,
					method: 'POST',
					data:data , 
					success: res => {
						console.log(res)
						var data=res.data 
						var list=data.data
						var lood=2
						if(data.code==1){
							if (list != '') {
							      lood = 1
							  } else {
							      lood = 3
							  }
							  if(list==''&& that.page>1){
								  lood = 1
							  }
							 that.prolodding=lood
							if(that.page==1){
								that.secdList=list
							}else{
								that.secdList=that.secdList.concat(list)
							}
							if(list.length>=1){
								that.flag=true
								that.medix=true
								// that.prolodding=1
								that.liloading=true
							}else{
								that.liloading=true
								that.flag=false
								that.medix=false 
								// that.prolodding=3
							}
						}
					},
					fail: () => {},
					complete: () => {}
				})
			}
			 
		 }
	},
	onPageScroll(e) {
		console.log(e)
		var that=this
		var scrollTop=e.scrollTop
		if(scrollTop<=300){
			that.gohidden=true
		}else{
			that.gohidden=false
		}
	},
	onLoad:function(option){
		console.log(option)
		var that=this
		var type=option.type
		var id=option.id
		var kw=option.kw
		that.kw=kw
		that.id=id
		if(type){
			that.type=type
		}
		that.goodList()
		 
		 
		
		
		  
	},
	onReachBottom:function(){
		var that=this
		if(that.flag){
			var page=that.page 
			page++
			that.page=page
			that.liloading=false 
			that.goodList() 
		}
	}
}
